import articleApi from '@/api/article'

export default {
  name: 'articleComments',
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      comments: []
    }
  },
  mounted() {
    this.getComments()
  },
  methods: {
    async getComments() {
      const { data } = await articleApi.getComments(this.article.slug)
      this.comments = data.comments
    }
  }
}
